<template>
  <!-- ÂØºËà™Ê†èÔºåÊ†πÊçÆÂΩìÂâç‰∏ªÈ¢òÂä®ÊÄÅÊ∑ªÂä†ÊöóÈªë‰∏ªÈ¢òÁ±ª -->
  <div class="navigation" :class="{ 'dark-theme': currentTheme === 'dark' }">
    
    <!-- Âæ™ÁéØÊòæÁ§∫ÂàÜÁ±ªÂèäÂÖ∂ÂØπÂ∫îÁöÑÈìæÊé• -->
    <div
      v-for="category in paginatedCategories"
      :key="category.name"
      class="category"
      :data-category="category.name"
    >
      <!-- ÂàÜÁ±ªÊ†áÈ¢òÔºåÁÇπÂáªÂèØÂ±ïÂºÄ/Êî∂Ëµ∑ÂàÜÁ±ª -->
      <h2 @click="toggleCategory(category.name)" class="category-title">
        <!-- Âä®ÊÄÅÊòæÁ§∫ÂàÜÁ±ªÂõæÊ†á -->
        <el-icon style="display: inline-flex; margin-right: 4px;">
          <component :is="category.icon" />
        </el-icon>
        {{ category.name }}
        <!-- Ê†πÊçÆÂàÜÁ±ªÁä∂ÊÄÅÊòæÁ§∫Â±ïÂºÄ/Êî∂Ëµ∑ÂõæÊ†á -->
        <i :class="[
          isCollapsed(category.name) ? 'el-icon-arrow-down' : 'el-icon-arrow-up',
          'toggle-icon'
        ]"></i>
      </h2>

      <!-- ÂΩìÂàÜÁ±ªÊú™Êî∂Ëµ∑Êó∂ÊòæÁ§∫ÈìæÊé•ÂàóË°® -->
      <div class="links-container">
        <ul>
          <li v-for="link in category.links" :key="link.name">
            <!-- ÈìæÊé•ÊåâÈíÆÔºåÂ∏¶ÊúâÂ∑•ÂÖ∑ÊèêÁ§∫ÊòæÁ§∫ÊèèËø∞ -->
            <el-tooltip class="item" effect="dark" :content="link.description" placement="top">
              <a :href="link.url" target="_blank"> 
                <el-button :type="currentTheme === 'dark' ? 'primary' : 'plain'">
                  <!-- ÊòæÁ§∫ÁΩëÁ´ôfavicon -->
                  <img 
                    v-if="getFaviconUrl(link.url)"
                    :src="getFaviconUrl(link.url)"
                    :alt="link.name"
                    class="favicon"
                    @error="handleImageError($event, link.url)"
                  />
                  {{ link.name }}
                </el-button>
              </a>
            </el-tooltip>
          </li>
        </ul>
      </div>
    </div>

    <!-- ÂàÜÈ°µÂô® -->
    <div class="pagination-container">
      <el-pagination
        v-model:current-page="currentPage"
        :page-size="pageSize"
        :total="filteredCategories.length"
        layout="prev, pager, next"
        @current-change="handlePageChange"
        background
      />
    </div>
  </div>
</template>

<script>
export default {
  name: 'NavigationLinks',
  // ÁªÑ‰ª∂Â±ûÊÄßÂÆö‰πâ
  props: {
    categories: {
      type: Array,
      required: true    // ÂØºËà™ÂàÜÁ±ªÊï∞ÊçÆÔºåÂøÖÂ°´
    },
    currentTheme: {
      type: String,
      required: true    // ÂΩìÂâç‰∏ªÈ¢òÔºåÂøÖÂ°´
    },
    searchQuery: {
      type: String,
      default: ''      // ÊêúÁ¥¢ÂÖ≥ÈîÆËØçÔºåÂèØÈÄâ
    },
    collapsedCategories: {
      type: Array,
      default: () => [] // Â∑≤ÊäòÂè†ÁöÑÂàÜÁ±ªÂàóË°®ÔºåÈªòËÆ§‰∏∫Á©∫Êï∞ÁªÑ
    },
    targetCategory: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      currentPage: 1,
      pageSize: 10
    }
  },
  computed: {
    // Ê†πÊçÆÊêúÁ¥¢ÂÖ≥ÈîÆËØçËøáÊª§ÂàÜÁ±ªÂíåÈìæÊé•
    filteredCategories() {
      if (!this.searchQuery) {
        return this.categories
      }
      const query = this.searchQuery.toLowerCase()
      return this.categories.map(category => {
        // ËøáÊª§ÈìæÊé•ÔºöÂåπÈÖçÂêçÁß∞„ÄÅURLÊàñÊèèËø∞
        const filteredLinks = category.links.filter(link => {
          return link.name.toLowerCase().includes(query) ||
                 link.url.toLowerCase().includes(query) ||
                 (link.description && link.description.toLowerCase().includes(query))
        })
        // Âè™ËøîÂõûÂåÖÂê´ÂåπÈÖçÈìæÊé•ÁöÑÂàÜÁ±ª
        if (filteredLinks.length > 0) {
          return {
            ...category,
            links: filteredLinks
          }
        }
        return null
      }).filter(category => category !== null)
    },
    paginatedCategories() {
      const start = (this.currentPage - 1) * this.pageSize;
      const end = start + this.pageSize;
      return this.filteredCategories.slice(start, end);
    }
  },
  methods: {
    // ÂàáÊç¢ÂàÜÁ±ªÁöÑÂ±ïÂºÄ/ÊäòÂè†Áä∂ÊÄÅ
    toggleCategory(categoryName) {
      this.$emit('toggle-category', categoryName)
    },
    // Ê£ÄÊü•ÂàÜÁ±ªÊòØÂê¶Â∑≤ÊäòÂè†
    isCollapsed(categoryName) {
      return this.collapsedCategories.includes(categoryName)
    },
    // Ëé∑ÂèñÁΩëÁ´ôfaviconÂõæÊ†áURL
    getFaviconUrl(url) {
      try {
        const domain = new URL(url).hostname;
        return `https://www.google.com/s2/favicons?domain=${domain}&sz=32`;
      } catch {
        return '/default-favicon.ico';
      }
    },
    // Ëé∑ÂèñÈìæÊé•Ëá™Â∏¶ÁöÑÂõæÊ†á
    getLinkIcon(url) {
      // Âú®ÂØºËà™Êï∞ÊçÆ‰∏≠Êü•ÊâæÂØπÂ∫îÁöÑÈìæÊé•
      for (const category of this.categories) {
        const link = category.links.find(link => link.url === url);
        if (link && link.icon) {
          return link.icon;
        }
      }
      return null;
    },
    // Â§ÑÁêÜÂõæÁâáÂä†ËΩΩÈîôËØØÔºåÂ∞ùËØïÂ§áÁî®ÊñπÊ≥ï
    handleImageError(event, url) {
      try {
        // 1. ÂÖàÂ∞ùËØï‰ΩøÁî®ÈìæÊé•Ëá™Â∏¶ÁöÑÂõæÊ†á
        const customIcon = this.getLinkIcon(url);
        if (customIcon) {
          event.target.src = customIcon;
          return;
        }

        // 2. ÂÜçÂ∞ùËØïËé∑ÂèñÁΩëÁ´ôÊ†πÁõÆÂΩïÁöÑ favicon.ico
        const urlObj = new URL(url);
        event.target.src = `${urlObj.protocol}//${urlObj.hostname}/favicon.ico`;
      } catch {
        event.target.src = '/default-favicon.ico';
      }
    },
    handlePageChange(page) {
      this.currentPage = page;
    },
    // Ë∑≥ËΩ¨Âà∞ÊåáÂÆöÂàÜÁ±ª
    scrollToCategory(categoryName) {
      if (!categoryName) return;
      
      // Êü•ÊâæÂàÜÁ±ªÂú®ËøáÊª§ÂêéÂàóË°®‰∏≠ÁöÑÁ¥¢Âºï
      const categoryIndex = this.filteredCategories.findIndex(
        category => category.name === categoryName
      );
      
      if (categoryIndex === -1) return;
      
      // ËÆ°ÁÆóÁõÆÊ†áÂàÜÁ±ªÊâÄÂú®ÁöÑÈ°µÁ†Å
      const targetPage = Math.floor(categoryIndex / this.pageSize) + 1;
      
      // Â¶ÇÊûúÈúÄË¶ÅÂàáÊç¢È°µÈù¢
      if (this.currentPage !== targetPage) {
        this.currentPage = targetPage;
      }
      
      // Á≠âÂæÖ DOM Êõ¥Êñ∞ÂêéÊªöÂä®Âà∞ÁõÆÊ†áÂàÜÁ±ª
      this.$nextTick(() => {
        const element = document.querySelector(`[data-category="${categoryName}"]`);
        if (element) {
          element.scrollIntoView({ behavior: 'smooth' });
        }
      });
    }
  },
  watch: {
    // ÁõëÂê¨ÁõÆÊ†áÂàÜÁ±ªÁöÑÂèòÂåñ
    targetCategory: {
      handler(newValue) {
        this.scrollToCategory(newValue);
      },
      immediate: true
    }
  }
}
</script>

