<template>
  <div class="add-navigation-container">
    <div class="add-navigation">
      <div class="header">
        <h2>Ê∑ªÂä†ÂØºËà™ÂÜÖÂÆπ</h2>
        <button class="close-btn" @click="$emit('close')">&times;</button>
      </div>
      <div class="form-section">
        <h2>Ê∑ªÂä†ÂØºËà™</h2>
        <form @submit.prevent="handleSubmit" class="nav-form">
          <div class="form-group">
            <label for="category">ÈÄâÊã©ÂàÜÁ±ªÔºö</label>
            <select 
              v-model="selectedCategory" 
              id="category" 
              required
              @change="onCategoryChange"
            >
              <option value="">ËØ∑ÈÄâÊã©ÂàÜÁ±ª</option>
              <option v-for="category in categories" 
                      :key="category.name" 
                      :value="category">
                {{ category.name }}
              </option>
            </select>
          </div>

          <div class="form-group">
            <label for="url">ÁΩëÁ´ôÂú∞ÂùÄÔºö</label>
            <div class="url-input-group">
              <input 
                type="url" 
                id="url" 
                v-model="formData.url"
                required
                pattern="https?://.*"
                placeholder="ËØ∑ËæìÂÖ•ÁΩëÁ´ôURLÔºà‰ª•http://Êàñhttps://ÂºÄÂ§¥Ôºâ"
                :disabled="isLoading"
                @blur="fetchMetadata"
              >
              <button 
                type="button" 
                class="fetch-btn" 
                @click="fetchMetadata"
                :disabled="!formData.url || isLoading || isFetching"
              >
                <span v-if="isFetching">Ëé∑Âèñ‰∏≠...</span>
                <span v-else>Ëé∑Âèñ‰ø°ÊÅØ</span>
              </button>
            </div>
          </div>

          <div class="form-group">
            <label for="name">ÁΩëÁ´ôÂêçÁß∞Ôºö</label>
            <input 
              type="text" 
              id="name" 
              v-model="formData.name"
              required
              placeholder="ËØ∑ËæìÂÖ•ÁΩëÁ´ôÂêçÁß∞"
            >
          </div>

          <div class="form-group">
            <label for="description">ÁΩëÁ´ôÊèèËø∞Ôºö</label>
            <textarea 
              id="description" 
              v-model="formData.description"
              required
              placeholder="ËØ∑ËæìÂÖ•ÁΩëÁ´ôÊèèËø∞"
            ></textarea>
          </div>

          <div class="form-actions">
            <button type="submit" :disabled="isLoading">
              {{ isLoading ? 'Ê∑ªÂä†‰∏≠...' : 'Ê∑ªÂä†' }}
            </button>
          </div>

          <div v-if="message" :class="['message', messageType]">
            {{ message }}
          </div>
        </form>
      </div>

      <div class="list-section">
        <h2>Áé∞ÊúâÂØºËà™</h2>
        <div v-for="category in categories" :key="category.name" class="category-group">
          <h3>{{ category.name }}</h3>
          <div class="links-grid">
            <div v-for="link in category.links" :key="link.url" class="link-card">
              <div class="link-content">
                <img :src="getLinkIcon(link.url) || getFavicon(link.url)" 
                     @error="handleImageError($event, link.url)" 
                     :alt="link.name"
                     class="link-icon">
                <div class="link-info">
                  <h4>{{ link.name }}</h4>
                  <a :href="link.url" target="_blank">{{ link.url }}</a>
                  <p>{{ link.description }}</p>
                </div>
              </div>
              <button @click="deleteNavigation(fileMap[category.name], category.name, link.url)" 
                      class="delete-btn">
                Âà†Èô§
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { aiTools } from '@/data/ai-tools'
import { entertainment } from '@/data/entertainment'
import { devTools } from '@/data/dev-tools'
import { designResources } from '@/data/design-resources'
import { downloads } from '@/data/downloads'
import { a } from '@/data/a'
import axios from 'axios'

// Êñá‰ª∂ÂêçÊò†Â∞Ñ
const fileMap = {
  // AIÂ∑•ÂÖ∑Áõ∏ÂÖ≥
  'AI ÊêúÁ¥¢': 'ai-tools',
  'ËßÜÈ¢ëÁΩëÁ´ô': 'ai-tools',
  'AI Â§ßÊ®°ÂûãÊé®ÁêÜ(‰∏≠Êñá)': 'ai-tools',
  'AI ÁªòÁîªÊ®°Âûã': 'ai-tools',
  'AI ÊñáÁîüËßÜÈ¢ë': 'ai-tools',
  'AI ÁøªËØë': 'ai-tools',
  'AI ËØ≠Èü≥ÂÖãÈöÜ': 'ai-tools',
  'AI Â∑•ÂÖ∑': 'ai-tools',
  'AI ÂÜô‰Ωú': 'ai-tools',
  'AI Èü≥‰πê': 'ai-tools',
  'AI ÁºñÁ®ã': 'ai-tools',
  'AI ËØæÁ®ã': 'ai-tools',
  'AI ÁªòÂõæÊèêÁ§∫ËØç': 'ai-tools',
  'Á§æ‰∫§Â™í‰Ωì': 'ai-tools',
  // Â®±‰πêÁõ∏ÂÖ≥
  'Ê∏∏Êàè': 'entertainment',
  
  'Âä®Êº´': 'entertainment',
  'Â®±‰πê': 'entertainment',
  
  // ÂºÄÂèëÂ∑•ÂÖ∑
  'ÂºÄÂèë': 'dev-tools',
  'ÂºÄÂèëÂ∑•ÂÖ∑': 'dev-tools',
  
  // ËÆæËÆ°ËµÑÊ∫ê
  'ËÆæËÆ°ËµÑÊ∫ê': 'design-resources',
  
  // ‰∏ãËΩΩÁõ∏ÂÖ≥
  '‰∏ãËΩΩÂ∑•ÂÖ∑': 'downloads',
  'ËΩØ‰ª∂‰∏ãËΩΩ': 'downloads',
  
  // Á§æ‰∫§Â™í‰Ωì
  
  
  // AÂàÜÁ±ª
  'ÁºñÁ®ãÂ∑•ÂÖ∑': 'a',
  'ÊµèËßàÂô®': 'a',
  'av': 'a',
  'A': 'a'
}

const api = axios.create({
  baseURL: 'http://localhost:3001',
  timeout: 5000
})

export default {
  name: 'AddNavigation',
  data() {
    // Êî∂ÈõÜÊâÄÊúâÂàÜÁ±ª
    const allData = [
      ...aiTools,
      ...entertainment,
      ...devTools,
      ...designResources,
      ...downloads,
      ...a
    ]

    return {
      fileMap,  // Ê∑ªÂä† fileMap Âà∞ÁªÑ‰ª∂Êï∞ÊçÆ‰∏≠
      categories: allData,
      selectedCategory: null,
      formData: {
        name: '',
        url: '',
        description: ''
      },
      message: '',
      messageType: 'success',
      isLoading: false,
      isFetching: false
    }
  },
  methods: {
    async fetchMetadata() {
      if (!this.formData.url || this.isFetching) return;
      
      this.isFetching = true;
      try {
        const response = await api.post('/api/fetch-metadata', {
          url: this.formData.url
        });
        
        if (response.data.success) {
          this.formData.name = response.data.title || '';
          this.formData.description = response.data.description || '';
        }
      } catch (error) {
        console.error('Ëé∑ÂèñÁΩëÁ´ô‰ø°ÊÅØÂ§±Ë¥•:', error);
        this.message = 'Ëé∑ÂèñÁΩëÁ´ô‰ø°ÊÅØÂ§±Ë¥•ÔºåËØ∑ÊâãÂä®ËæìÂÖ•';
        this.messageType = 'error';
        setTimeout(() => {
          this.message = '';
        }, 3000);
      } finally {
        this.isFetching = false;
      }
    },

    onCategoryChange() {
      if (!this.selectedCategory) {
        this.message = 'ËØ∑ÈÄâÊã©‰∏Ä‰∏™ÂàÜÁ±ª';
        this.messageType = 'error';
        return;
      }
      this.message = '';
    },

    async handleSubmit() {
      if (!this.selectedCategory) {
        this.message = 'ËØ∑ÈÄâÊã©‰∏Ä‰∏™ÂàÜÁ±ª';
        this.messageType = 'error';
        return;
      }

      this.isLoading = true;
      try {
        // ÂàõÂª∫Êñ∞ÁöÑÈìæÊé•ÂØπË±°
        const newLink = {
          name: this.formData.name,
          url: this.formData.url,
          description: this.formData.description
        };

        // Ëé∑ÂèñÂØπÂ∫îÁöÑÊñá‰ª∂Âêç
        const fileName = this.fileMap[this.selectedCategory.name];
        if (!fileName) {
          throw new Error('Êú™ÊâæÂà∞ÂØπÂ∫îÁöÑÊñá‰ª∂Âêç');
        }

        // ÂèëÈÄÅÂà∞ÂêéÁ´Ø‰øùÂ≠ò
        const response = await api.post('/api/save-navigation', {
          fileName: fileName,
          categoryName: this.selectedCategory.name,
          link: newLink
        });

        if (!response.data.success) {
          throw new Error(response.data.message || '‰øùÂ≠òÂ§±Ë¥•');
        }

        // Â∞ÜÊñ∞ÈìæÊé•Ê∑ªÂä†Âà∞ÈÄâÂÆöÂàÜÁ±ªÁöÑlinksÊï∞ÁªÑ‰∏≠
        this.selectedCategory.links.push(newLink);

        // ÂèëÂá∫ÊàêÂäüÊ∂àÊÅØ
        this.message = 'Ê∑ªÂä†ÊàêÂäüÔºÅ';
        this.messageType = 'success';

        // ÈáçÁΩÆË°®Âçï
        this.formData = {
          name: '',
          url: '',
          description: ''
        };
        this.selectedCategory = null;

        // 3ÁßíÂêéÂÖ≥Èó≠ÂØπËØùÊ°Ü
        setTimeout(() => {
          this.$emit('close');
        }, 3000);
      } catch (error) {
        console.error('Ê∑ªÂä†ÂØºËà™Â§±Ë¥•:', error);
        this.message = error.message || 'Ê∑ªÂä†Â§±Ë¥•ÔºåËØ∑ÈáçËØï';
        this.messageType = 'error';
      } finally {
        this.isLoading = false;
      }
    },
    async loadNavigationData() {
      try {
        // ÂØºÂÖ•ÊâÄÊúâÊï∞ÊçÆÊñá‰ª∂
        const [
          aiToolsModule,
          entertainmentModule,
          devToolsModule,
          designResourcesModule,
          downloadsModule,
          aModule
        ] = await Promise.all([
          import('../data/ai-tools.js'),
          import('../data/entertainment.js'),
          import('../data/dev-tools.js'),
          import('../data/design-resources.js'),
          import('../data/downloads.js'),
          import('../data/a.js')
        ]);

        // ÂêàÂπ∂ÊâÄÊúâÂàÜÁ±ª
        this.categories = [
          ...aiToolsModule.categories,
          ...entertainmentModule.categories,
          ...devToolsModule.categories,
          ...designResourcesModule.categories,
          ...downloadsModule.categories,
          ...aModule.categories
        ];
      } catch (error) {
        console.error('Âä†ËΩΩÂØºËà™Êï∞ÊçÆÂ§±Ë¥•:', error);
        this.message = 'Âä†ËΩΩÂØºËà™Êï∞ÊçÆÂ§±Ë¥•';
        this.messageType = 'error';
      }
    },
    async deleteNavigation(fileName, categoryName, url) {
      try {
        console.log('Âà†Èô§ÂèÇÊï∞:', { fileName, categoryName, url });
        const response = await api.post('/api/delete-navigation', {
          fileName,
          categoryName,
          url
        });
        
        if (response.data.success) {
          this.message = 'Âà†Èô§ÊàêÂäü';
          this.messageType = 'success';
          
          // ÈáçÊñ∞Âä†ËΩΩÊï∞ÊçÆ
          await this.loadNavigationData();
          
          // 3ÁßíÂêéÊ∏ÖÈô§Ê∂àÊÅØ
          setTimeout(() => {
            this.message = '';
          }, 3000);
        } else {
          this.message = response.data.error || 'Âà†Èô§Â§±Ë¥•';
          this.messageType = 'error';
        }
      } catch (error) {
        console.error('Âà†Èô§ÂØºËà™Â§±Ë¥•:', error);
        this.message = error.response?.data?.error || 'Âà†Èô§Â§±Ë¥•ÔºåËØ∑ÈáçËØï';
        this.messageType = 'error';
      }
    },
    // Ëé∑ÂèñÁΩëÁ´ôfaviconÂõæÊ†áURL
    getFavicon(url) {
      try {
        const domain = new URL(url).hostname;
        return `https://www.google.com/s2/favicons?domain=${domain}&sz=32`;
      } catch {
        return '/default-favicon.ico';
      }
    },
    // Ëé∑ÂèñÈìæÊé•Ëá™Â∏¶ÁöÑÂõæÊ†á
    getLinkIcon(url) {
      // Âú®ÂØºËà™Êï∞ÊçÆ‰∏≠Êü•ÊâæÂØπÂ∫îÁöÑÈìæÊé•
      for (const category of this.categories) {
        const link = category.links.find(link => link.url === url);
        if (link && link.icon) {
          return link.icon;
        }
      }
      return null;
    },
    // Â§ÑÁêÜÂõæÁâáÂä†ËΩΩÈîôËØØÔºåÂ∞ùËØïÂ§áÁî®ÊñπÊ≥ï
    handleImageError(event, url) {
      try {
        // 1. ÂÖàÂ∞ùËØï‰ΩøÁî®ÈìæÊé•Ëá™Â∏¶ÁöÑÂõæÊ†á
        const customIcon = this.getLinkIcon(url);
        if (customIcon) {
          event.target.src = customIcon;
          return;
        }

        // 2. ÂÜçÂ∞ùËØïËé∑ÂèñÁΩëÁ´ôÊ†πÁõÆÂΩïÁöÑ favicon.ico
        const urlObj = new URL(url);
        event.target.src = `${urlObj.protocol}//${urlObj.hostname}/favicon.ico`;
      } catch {
        event.target.src = '/default-favicon.ico';
      }
    },
  }
}
</script>

