      </el-icon>
    </el-menu-item>

    <!-- ÊêúÁ¥¢È°πÔºå‰Ωç‰∫éËèúÂçï‰∏≠ÔºåÊ†∑ÂºèÊ†πÊçÆ‰∏ªÈ¢òÂä®ÊÄÅË∞ÉÊï¥ -->
    <el-menu-item index="search" class="search-item" style="margin-left: auto;">
      <div class="search-container">
        <!-- ÊêúÁ¥¢ÂºïÊìéÈÄâÊã©‰∏ãÊãâÊ°ÜÔºåÊ†πÊçÆÂΩìÂâç‰∏ªÈ¢òË∞ÉÊï¥Ê†∑Âºè -->
        <el-select
