            class="search-engine-option"
          >
            <!-- ÊêúÁ¥¢ÂºïÊìéÂõæÊ†áÔºåÂä®ÊÄÅÂä†ËΩΩ -->
            <el-icon class="search-engine-icon"><component :is="engine.icon" /></el-icon>
            {{ engine.label }}
          </el-option>
        </el-select>
        <!-- ÊêúÁ¥¢ËæìÂÖ•Ê°ÜÔºåÂ∏¶ÊúâÊêúÁ¥¢ÊåâÈíÆÔºåÊ†πÊçÆÂΩìÂâç‰∏ªÈ¢òË∞ÉÊï¥Ê†∑Âºè -->
        <el-input
          v-model="searchText"
          placeholder="ÊêúÁ¥¢..."
          class="search-input"
          @keyup.enter="handleSearch"
          @input="handleSearchInput"
          :style="[
            currentTheme === 'dark' ? {
              '--el-input-bg-color': '#1a1a1a',
