<script>
// ÂØºÂÖ• Element Plus ÂõæÊ†áÁªÑ‰ª∂
import { House, InfoFilled, Coffee, Plus, Moon, Sunny, Search, ChromeFilled } from '@element-plus/icons-vue'

export default {
  name: 'NavigationHeader',
  // Ê≥®ÂÜå‰ΩøÁî®ÁöÑÂõæÊ†áÁªÑ‰ª∂
  components: {
    House,
    InfoFilled,
    Coffee,
    Plus,
    Moon,
    Sunny,
    Search,
    ChromeFilled
  },
  // ÁªÑ‰ª∂Â±ûÊÄßÂÆö‰πâ
  props: {
    // ÂΩìÂâçÊøÄÊ¥ªÁöÑËèúÂçïÈ°πÁ¥¢Âºï
    activeIndex: {
      type: String,
      default: ''
    },
    // ÂΩìÂâç‰∏ªÈ¢òÔºàlight/darkÔºâ
    currentTheme: {
      type: String,
      required: true
    }
  },
  // ÂÆö‰πâÁªÑ‰ª∂ÂèØËß¶ÂèëÁöÑ‰∫ã‰ª∂
  emits: ['show-about', 'show-donate', 'show-reward', 'select', 'toggle-theme', 'test', 'search-input'],
  data() {
    return {
      searchText: '',
      selectedEngine: 'google',
      searchEngines: [
        {
          label: 'Google',
          value: 'google',
          url: 'https://www.google.com/search?q=%s',
          icon: 'ChromeFilled'
        },
        {
          label: 'Bing',
          value: 'bing',
          url: 'https://www.bing.com/search?q=%s',
          icon: 'Search'
        },
        {
          label: 'Baidu',
          value: 'baidu',
          url: 'https://www.baidu.com/s?wd=%s',
          icon: 'Search'
        },
        {
          label: 'GitHub',
          value: 'github',
          url: 'https://github.com/search?q=%s',
          icon: 'Search'
        }
      ]
    }
  },
  methods: {
    // Â§ÑÁêÜËèúÂçïÈ°πÈÄâÊã©‰∫ã‰ª∂
    handleSelect(key) {
      this.$emit('select', key)
    },
    // Â§ÑÁêÜÈ¶ñÈ°µË∑≥ËΩ¨
    handleHome() {
      window.location.href = 'http://192.168.31.81:8080/';
    },
    // Â§ÑÁêÜÊêúÁ¥¢
    handleSearch() {
      if (!this.searchText.trim()) return;
      const searchUrl = this.searchEngines.find(engine => engine.value === this.selectedEngine)?.url;
      if (searchUrl) {
        window.open(searchUrl.replace('%s', encodeURIComponent(this.searchText)), '_blank');
      }
    },
    handleSearchInput(value) {
      this.$emit('search-input', value);
    }
  }
}
</script>

